body {
  font: 2vmin "Century Gothic", sans-serif;
  margin: 0.2vmin;
}

ol, ul {
  padding-left: 30px;
}

p {  
  display: block;
  margin-block-start: 0.7vmin;
  margin-block-end: 0.7vmin;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}

.swal2-popup {
  width: 40vmin;
  font-size: 2vmin;
}

.status-info {
  margin-left: 0.2vmin;
  color: rgb(208,33,41);
}


.button-container {
  margin-left: 5vmin;
  font-size: 5vmin;
  width: 90vmin;
}

.status {
  margin-bottom: 10px;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  width: 100vmin;
  flex-direction: column;
  margin-top: 2.5vmin;
  justify-content: center;
  /* align-items: center; */
  min-height: 15vh;
}

.scores-container {
  margin-right: 10vmin;
  color: rgb(208,33,41);
}

.create-button {
  margin-right: 2vmin;
  width: fit-content;
  height: 5vmin;
  background-color: aqua;
  border-radius: 0.5vmin;
}

.join-button {
  width: 10vmin;
  height: 5vmin;
  background-color: rgb(255, 200, 47);
  border-radius: 0.5vmin;
}

.input-class{
  margin-top: -0.1vmin;
}

.title-class {
  font-size: 1.5rvmin; 
}

.popup-class{
  height: 165px; 
}

.join-button-class {
  margin: .3125vmin; 
  padding: .5vmin 1.8vmin; 
  margin-bottom: 1vmin; 
}

.button-class {
  margin: .3125vmin; 
  padding: .5vmin 1.8vmin; 
}

.control {
  position: relative;
  top: -80vmin;
  left: 12vmin;
  width: 65vmin;
}

.pawn {
  position: absolute;
  transition: 1s ease-in-out;
  background: #dbdbdb;
  width: fit-content;
  border-radius: 0.2vmin;
  box-shadow: 0.2vmin 0.2vmin #888888;
}

#pawn1 {
  border: 2px solid red;
  left: 1vmin;
  top: 3vmin;
}

#pawn2 {
  border: 2px solid blue;
  left: 1.3vmin;
  top: 5vmin;
}

#pawn3 {
  border: 2px solid purple;
  left: 1vmin;
  top: 7vmin;
}

#pawn4 {
  border: 2px solid green;
  left: 1.3vmin;
  top: 9vmin;
}


.square {
	background: white;
  text-align: center;
  overflow: scroll;
}

.detail {
  width: 9vmin;
  visibility: hidden;
  position: absolute;
  display: block;
  position: absolute;
  border: 2px solid grey;
  background: lightsteelblue;
  z-index: 5;
  line-height: 1.5vmin;
}

.showDetail > .detail {
  visibility: visible;
}

.playersInfo {
  padding: 0.05vmin;
  margin: 0.05vmin;
  height: fit-content;
}

.playersInfo:hover > .detail{
  visibility: visible;
}

.playersInfo:focus > .detail{
  visibility: visible;
}

.control > button {
  width: 21vmin;
  height: 3.0vmin;
  background-color: lightgreen;
  border-radius: 0.5vmin;
  margin: 0.2vmin;
  font-size: 1vmin;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

button:hover {
  cursor: pointer;
  border: 2px black solid;
}

/* button:hover > .toolTip{
  visibility: visible;
} */

button:disabled {
  cursor: auto;
  background-color: white;
}

button:disabled:hover {
  cursor: not-allowed;
  border: none;
  background-color: white;
}

.bottomPanel {
  display: flex;
  flex-direction: column;
}

.playersInfoWrapper {
  display: flex;
  text-align: center;
  overflow: scroll;
  border: 0.4vmin solid black;
  padding: 0.5vmin;
  width: fit-content;
  height: fit-content;
}

.playersInfo:nth-child(1) {
  border: 0.4vmin red solid;
}

.playersInfo:nth-child(2) {
  border: 0.4vmin blue solid;
}

.playersInfo:nth-child(3) {
  border: 0.4vmin purple solid;
}

.playersInfo:nth-child(4) {
  border: 0.4vmin green solid;
}

.chatArea {
  display: flex;
  flex-direction: column;
}

.chatInputArea > button {
  width: 12vmin;
  background-color: lightseagreen;
  border-radius: 0.5vmin;
  font-size: 1.5vmin;
}

.chatInputArea > input {
  font-size: 1vmin;
}

.chatArea > textarea {
  font-size: 2vmin;
  height: 15vmin;
}

#tradePanel {
  position: absolute;
  top: 10vmin;
}

.tradeWrapper {
  width: 60vmin;
  height: 11vmin;
  border: 0.4vmin solid yellowgreen;
  font-size: 1vmin;
  overflow: scroll;
  padding: 0.5vmin;
  background-color: white;
}

.tradeWrapper > button {
  width: 12vmin;
  height: fit-content;;
  background-color: salmon;
  border-radius: 0.5vmin;
  font-size: 1vmin;
}

.toolTip {
  width: 10vmin;
  visibility: hidden;
  position: absolute;
}

.toolTip > p {
  background-color: white;
  border: 1px black solid;
  color: black;
  position: relative;
  top: 9vmin;
  padding: 0.2vmin;
  border-radius: 0.5vmin;
  transition: ease-in;
}

.rules {
  position: absolute;
  top: 95vmin;
  left: 5vmin;
  width: 85vmin;
}